button {
  font-size: inherit;
  font-family: inherit;
  cursor: pointer;
  background-color: $thpRed;
  border-radius: 3px;
  border: 0.5px solid $thpRed;
  display: inline-block;
  color: $textColor;
  font-size: 19px;
  padding: 6px 24px;
  text-decoration: none;
  text-shadow: 0px 1px 0px $textColor;
}
button:hover {
  color: $activeBlue;
  text-decoration: none;
  text-shadow: 0px 1px 0px $textColor;
}
