.app-main {
  width: 70%;
  height: 100vh;
}
.app-main-note-edit,
.app-main-note-preview {
  height: 50vh;
}

.no-active-note {
  width: 70%;
  height: 100vh;
  line-height: 100vh;
  text-align: center;
  font-size: 2rem;
  color: #999;
}

/* Editing */
.app-main-note-edit {
  padding: 25px;
}

.app-main-note-edit input,
textarea {
  display: block;
  border: 1px solid #ddd;
  margin-bottom: 20px;
  width: 100%;
  height: calc(50vh - 130px);
  padding: 10px;
  resize: none;
  font-size: inherit;
  font-family: inherit;
}

.app-main-note-edit input {
  height: 50px;
  font-size: 2rem;
  color: $thpRed;
}

/* Preview */
.app-main-note-preview {
  border-top: 3px solid #ddd;
  overflow-y: scroll;
  background: rgba(0, 0, 0, 0.02);
}

.preview-title {
  padding: 25px 25px;
  margin-left: 15px;
  margin-right: 15px;
  background-color: $markDownBGColor;
  color: $textColor;
  font-size: 1rem;
}

.markdown-preview {
  padding: 5px 25px 10px 25px;
  margin-left: 15px;
  margin-right: 15px;
  font-size: 1rem;
  line-height: 2rem;
  background-color: $markDownBGColor;
  color: $textColor;
}
